import React from "react";

class Experience extends React.Component {
	render() {
		return (
			<div className="col-6 ">
				<div className="itemExperience">
					<a href="#">
						<div className="picExperience">
							<img src={this.props.image} />
						</div>
						<h4>{this.props.name}</h4>
					</a>
				</div>
			</div>
		);
	}
}

export default Experience;
